
import XHeader from 'vux/src/components/x-header/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapGetters } from 'vuex'
import api from '../../fetch/api'
export default {
    components: {
        XHeader,
        dateFormat,
        Toast,
        XButton
    },
    data() {
        return {
            show_success: false,
        }
    },
    created() {
        this.$store.dispatch('get_activity_detail', {id: this.$route.query.activityid,uid: localStorage.getItem('loginopenid')})
    },
    computed: {
        ...mapGetters([
            'activity_detail',
        ])
    },
    methods: {
        _activity_enroll() {
            let data = {
                uid: localStorage.getItem('loginopenid'),
                aid: this.$route.query.activityid
            }
            console.log(data);
            api.v3_activity_enroll(data).then(res => {
                console.log(res)
                if (res.retcode == 200) {
                    this.show_success = true;
                }
            }).catch(error => {
                console.log(error)
            })
        }
    },
    filters: {
        filterType: function (type) {
            switch (parseInt(type)) {
                case 1: return "徒步"; break;
                case 2: return "跑步"; break;
                case 3: return "骑行"; break;
                case 4: return "其他"; break;
            }
        },
        filterTime_p: function (starttime, endtime) {
            let startFormat = starttime * 1000;
            let endFormat = endtime * 1000;
            var start = dateFormat(startFormat, 'MM.DD HH:mm');
            var end = dateFormat(endFormat, 'MM.DD HH:mm');
            return `${start}-${end}`;
        },
    }
}




